<template>
  <div>
      <el-table
      :data="list"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
    >
      <el-table-column prop="id" label="编号" width="120">
      </el-table-column>
      <el-table-column prop="title" label="轮播图标题"> </el-table-column>
      
      <el-table-column prop="img" label="图片"> 
        <template slot-scope="scope">
            <img :src="$img+scope.row.img" alt="" class="img">
        </template>
      </el-table-column>

      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1"
            >启用</el-button
          >
          <el-button type="danger" v-else>禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="id" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="emitMenu(scope.row.id)"
            >编辑</el-button
          >
          <el-button type="danger" @click="deltMenu(scope.row.id)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import {mapGetters,mapActions} from "vuex"
import { successAlert } from '../../util/alert'
import {delBnner} from "../../util/request"
export default {
    computed:{
        ...mapGetters({
            list:"banner/list"
        })
    },
    methods:{
        ...mapActions({
            requestBanner:"banner/bannerActions"
        }),
        emitMenu(id){
            this.$emit("deta",id)
        },
        deltMenu(id){
            this.$confirm("确定要删除吗？","提示",{
                confirmButtonText:"确定",
                cancelButtonText:"取消",
                type: "warning",
            }).then(()=>{
                delBnner({id:id}).then(res=>{
                    successAlert(res.data.msg);
                     this.requestBanner()
                })
            }).catch(()=>{
                this.$message({
                    type:"info",
                    message:"已取消删除"
                })
            })
        }
    },
    mounted(){
        this.requestBanner()
    }
}
</script>

<style scoped>
.img{
    width: 100px;
    height: 100px;
}
</style>


