<template>
  <div>
<el-dialog :title="info.title" :visible.sync="info.isShow">
      <el-form :model="form">
        <el-form-item label="标题" :label-width="formLabelWidth">
          <el-input v-model="form.title" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="图片" :label-width="formLabelWidth">
            <el-upload v-model="form.img"
                class="avatar-uploader"
                action="#"
                :on-change="changeImg">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
        </el-form-item>

        <el-form-item  label="状态" :label-width="formLabelWidth">
          <el-switch v-model="form.status"
            active-color="#409eff"
            inactive-color="grey"
            :active-value="1" :inactive-value="2"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" v-if="info.isAdd" @click="ok">添 加</el-button>
        <el-button type="primary" v-else @click="update" >修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { successAlert, warningAlert } from '../../util/alert';
import {bannerAdd,oneBnner,bannerUpdate} from "../../util/request"
import {mapGetters,mapActions} from "vuex"
export default {
    props:["info"],
    data(){
        return {
            form:{
                title:"",
                img:"",
                status:1
            },
            formLabelWidth:"120px",
            imageUrl:""
        }
    },
    methods:{
        ...mapActions({
            requestBanner:"banner/bannerActions"
        }),
        changeImg(e){
            if(e.size>2*1024*1024){
                warningAlert("文件过大")
                return
            }
            var ex=[".jpg",".png"]
            var ext=e.name.slice(e.name.lastIndexOf("."))
            if (!ex.some(item=>item==ext)) {
                warningAlert("文件格式不正确")
                return
            }
            this.imageUrl = URL.createObjectURL(e.raw);
            this.form.img=e.raw
        },
        cancel(){
            this.info.isShow=false
            this.form={
                title:"",
                img:"",
                status:1
            };
            this.imageUrl=''
        },
        ok(){
            bannerAdd(this.form).then(res=>{
                if(res.data.code==200){
                    successAlert(res.data.msg)
                    this.cancel()
                    this.requestBanner()
                }else{
                    warningAlert(res.data.msg)
                }
            })
        },
        detail(id){
            oneBnner({id:id}).then(res=>{
                this.form=res.data.list
                this.imageUrl=this.$img+this.form.img
                this.form.id=id
            })
        },
        update(){
            bannerUpdate(this.form).then(res=>{
                if(res.data.code==200){
                    successAlert(res.data.msg)
                    this.cancel()
                    this.requestBanner()
                }
            })
        }
    },
}
</script>

<style scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .el-icon-plus{
      border: 1px dashed #d9d9d9;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>


